import PageLayout from '../components/PageLayout';
import Utils from '../util/Utils';

import '../styles/grid.scss';

class Grid extends React.Component {
    state = {
        names: Utils.getNames()
    };
    componentDidMount() {
        Utils.loadVoices();
    }
    render() {
        const colCount = Math.ceil(Math.sqrt(this.state.names.length));
        const rowCount = Math.ceil(names.length / rowCount);

        const style = {
            width: 100 / colCount + '%',
            height: 100 / rowCount + '%'
        }

        const names = this.state.names.map(n => n.value);

        return (
            <PageLayout className="grid">
                {names.map(n => <div className="name" style={style}>{n}</div>)}
            </PageLayout>
        );
    }
}

export default Grid;